import React from "react";
import faker from "faker";
import "./Post.css";

function Post(props) {
  return (
    <div className="post__container">
      <div className="post__top">
        <img className="post__avatar" src={faker.image.avatar()} alt="" />
        <strong>{props.user}</strong>
      </div>
      <img className="post__image" src={faker.image.image()} alt="i" />
      <div className="post__bottom">
        <strong>{props.user}</strong> Nice Pic.
        <br />
        <span>{faker.random.number(10)} min ago</span>
      </div>
    </div>
  );
}

export default Post;
